import {Component} from '@angular/core';
import {AlertController, LoadingController, IonicPage, Platform, NavController} from 'ionic-angular';
import {WalletService} from "../../providers/wallet.service";
import {UserService} from "../../providers/user.service";
import {Geolocation} from '@ionic-native/geolocation';
import { InAppBrowser } from '@ionic-native/in-app-browser';

@IonicPage()
@Component({
  selector: 'page-ivpay',
  templateUrl: 'ivpay.html'
})
export class IvpayPage {

  // initial center position for the map
  lat: number;
  lng: number;
  zoom: number;
  markers = [];
  alive: boolean = true;

  pages1:any[] = [
    { icon: 'qr-scanner', title: 'Scan', page: 'ScanPage', active: true, },
    { icon: 'barcode', title: 'Pay', page: 'PayPage', active: false, },
    { icon: 'log-in', title: 'Request', page: 'RequestPage', active: false, },
    { icon: 'repeat', title: 'Transfer', page: 'TransferPage', active: false, }
  ];

  pages2:any[] = [
    { icon: 'qr-scanner', title: 'Scan', page: 'ScanPage', active: true, },
    { icon: 'barcode', title: 'Pay', page: 'PayPage', active: false, },
    { icon: 'log-in', title: 'Request', page: 'RequestPage', active: false, },
    { icon: 'repeat', title: 'Transfer', page: 'TransferPage', active: false, },
    { icon: 'logo-usd', title: 'Fiat/Crypto exchange', page: 'FiatPage', active: false, },
    { icon: 'card', title: 'Wallet', page: 'WalletPage', active: false, }
  ];

  pages3:any[] = [
    { icon: 'md-analytics', title: 'Crypto Trend', page: 'ScanPage', active: true, },
    { icon: 'time', title: 'History', page: 'PayPage', active: false, }
  ];

  constructor( public navCtrl: NavController,
               private alertCtrl: AlertController,
               private walletService: WalletService,
               private userService: UserService,
               public geolocation: Geolocation,
               public platform: Platform,
               public loadingCtrl: LoadingController,
               private iab: InAppBrowser) {
  }

  ionViewDidLoad() {
    this.zoom = 3;
    // initial center position for the map
    this.lat = 0;
    this.lng = 0;

    this.loadMap();
    this.loadFiatPosition();
  }

  ionViewWillLeave() {
    this.alive = false;
  }

  showList(pages) {
    if(pages.page == 'PayPage' || pages.page === 'RequestPage') {
      let alert = this.alertCtrl.create({
        cssClass: 'radio-alert'
      });
      alert.setTitle('Choose your wallet');

      for (let trigram of this.walletService.trigramAvailable) {
        alert.addInput({
          type: 'radio',
          label: trigram.toUpperCase(),
          value: trigram.toLowerCase(),
          checked: trigram.toLowerCase() == 'eth' ? true : false
        });
      }

      alert.addButton({
        text: 'Ok',
        handler: data => {
          this.navCtrl.push(pages.page, {trigram: data});
        }
      });

      alert.present();

    } else if(pages.page === 'FiatPage') {
      let alert = this.alertCtrl.create({
        cssClass: 'radio-alert'
      });
      alert.setTitle('Fiat exchange');

      alert.addInput({
        type: 'radio',
        label: 'Get ' + this.userService._profil.currency + ' with ' + this.userService._profil.paymentMethod,
        value: 'fiat',
        checked: true
      });

      alert.addInput({
        type: 'radio',
        label: 'Get ' + this.userService._profil.paymentMethod + ' with ' + this.userService._profil.currency,
        value: 'crypto',
        checked: false
      });


      alert.addButton({
        text: 'Ok',
        handler: data => {
          if(data == 'crypto') {
            this.navCtrl.push(pages.page, {trigram: this.userService._profil.paymentMethod.toLowerCase(), request: true});
          } else {
            this.navCtrl.push(pages.page, {trigram: this.userService._profil.paymentMethod.toLowerCase()});
          }

        }
      });

      alert.present();
    } else {
      this.navCtrl.push(pages.page);
    }
  }

  loadMap(){
    let loader = this.loadingCtrl.create({content: 'Please wait...', duration: 5000});
    loader.present();
    this.geolocation.getCurrentPosition()
      .then((position) => {
        loader.dismiss().catch();

        this.lat = position.coords.latitude;
        this.lng = position.coords.longitude;
        this.zoom = 8;

        this.markers.push({
          lat: this.lat,
          lng: this.lng,
          draggable: false,
          visible: true,
          markerClickable: false,
          iconUrl: "assets/img/position.png"
        });

      }).catch((error) => {
        loader.dismiss().catch();
        this.lat = 0;
        this.lng = 0;
        this.zoom = 3;

        const alert = this.alertCtrl.create({
          title: 'Error',
          subTitle: 'Error while getting your position, try again',
          buttons: ['Close']
        });
        alert.present();
    });
  }

  loadFiatPosition() {
    this.userService.getPositions().takeWhile(() => this.alive).subscribe(positions => {
      positions.forEach(position => {
        this.userService.getProfil(position.$key).subscribe(profil => {
          this.addPositionFiat(position.lat, position.lng, profil, position.$key)
        });
      });
    })
  }

  addPositionFiat(lat, lng, profil, uid) {
    this.markers.push({
      lat: lat,
      lng: lng,
      draggable: false,
      position: {profil: profil, uid: uid}
    })
  }

  contact(profil, uid) {
    let uidArray = [this.userService._userFirebase.uid, uid];
    uidArray = uidArray.sort();

    if(profil.telegram) {
      let confirm = this.alertCtrl.create({
        title: 'Contact',
        message: 'Chat with Telegram or NeoPlace chat ?',
        cssClass: 'confirm-alert',
        buttons: [
          {
            text: 'Telegram',
            handler: () => {
              const browser = this.iab.create('https://t.me/' + profil.telegram, '_system');
            }
          },
          {
            text: 'NeoPlace chat',
            handler: () => {
              this.navCtrl.push('MessagesPage', {id: uidArray[0] + "-" + uidArray[1], profil: profil, otherUid: uid});
            }
          }
        ]
      });
      confirm.present();
    } else {
      this.navCtrl.push('MessagesPage', {id: uidArray[0] + "-" + uidArray[1], profil: profil, otherUid: uid});
    }
  }

}



